@import url(https://fonts.googleapis.com/css2?family=Righteous&family=Rubik:wght@300;400;500;600;700&display=swap);
:root{--black: hsl(0, 0%, 12%);--purple-blue: hsl(223, 100%, 63%);--white: hsl(0, 0%, 94%)}*,*::after,*::before{box-sizing:border-box;margin:0;padding:0}body{font-family:"Rubik",sans-serif;min-height:100vh;position:relative}.container{width:95%;margin:0 auto;max-width:1200px;padding-bottom:9rem}a,a:hover,a:visited,a:active{color:inherit;text-decoration:none}.header{width:100%;min-height:75px;background-color:hsl(0, 0%, 12%);background-color:var(--black);color:#fff;display:flex;align-items:center;justify-content:center;position:relative;top:0;left:0;z-index:1}.header .logo{font-family:"Righteous",cursive;color:#fff}.add-book{position:fixed;background-color:hsl(223, 100%, 63%);background-color:var(--purple-blue);width:3rem;height:3rem;bottom:4.5rem;right:3rem;border-radius:50%;z-index:1}.add-book .link{width:100%;height:100%;display:flex;justify-content:center;align-items:center;color:#fff;font-size:2rem}.shelf{color:hsl(0, 0%, 12%);color:var(--black)}.shelf .shelf-title{margin-top:2rem;padding-left:.5rem;border-left:.4rem solid hsl(223, 100%, 63%);border-left:.4rem solid var(--purple-blue)}.shelf .books-container{margin-top:2.5rem;display:grid;grid-template-columns:repeat(auto-fill, minmax(150px, 160px));grid-gap:1.7rem;gap:1.7rem}.book-card img{background-color:hsl(223, 100%, 63%);background-color:var(--purple-blue);width:100%;height:200px;object-fit:fill}.book-card .shelf-status{padding:.5rem;background-color:hsl(223, 100%, 63%);background-color:var(--purple-blue);color:#fff;width:100%;border:none;font-weight:600;font-size:1rem;border-radius:.2rem;text-transform:capitalize}.book-card .shelf-status option{display:block;padding-top:10px}.book-card .shelf-status option:hover{color:hsl(223, 100%, 63%);color:var(--purple-blue);background-color:#fff}.book-card .book-title{margin-top:.5rem;font-weight:400}.book-card .book-author{display:block;color:#787878;margin-top:.2rem}.search-container{margin:1.5rem auto;width:100%;display:flex;justify-content:center;align-items:center}.search-container .search-bar{display:inline-block;width:80%;padding:.3rem .5rem;max-width:600px;border:1px solid #22222288;font-size:1.3rem;border-radius:5px}.search-container .back-link{display:flex;font-size:2rem;line-height:0;color:hsl(223, 100%, 63%);color:var(--purple-blue)}.icon-holder{position:absolute;left:0;right:0;width:100%;height:50%;display:flex;flex-direction:column;align-items:center;justify-content:center}.icon-holder .search-icon{font-size:10rem;opacity:.2}.loading{font-weight:400;color:#777}.not-found{background:linear-gradient(0deg, #4278ff 0%, #2dd9fd 100%);color:hsl(0, 0%, 94%);color:var(--white);position:absolute;left:0;top:0;right:0;bottom:0;display:flex;align-items:center;justify-content:center;flex-direction:column;z-index:0}.not-found .error-404{font-size:10rem}.not-found .text{font-weight:400}.not-found .link{margin-top:1rem;text-decoration:underline}.footer{background-color:hsl(0, 0%, 12%);background-color:var(--black);width:100%;padding:2rem;color:#888;position:absolute;bottom:0;text-align:center;display:flex;justify-content:center;align-content:center;font-size:1.1em}.footer .react-icon{color:#61dbfb}.footer .github-link{color:#ccc}.footer .github-link:hover{color:#fff}
